import { Component, h, Prop, Build, Element } from '@stencil/core';
import { register, RegisterData } from "../../../global/data/register";
import { loadCommonData } from '../../../global/functions/loadCommon';

@Component({
    tag: 'ks-page-register',
    styleUrl: 'register.css'
})
export class PageRegister {
    @Element() root: HTMLElement;

    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() registerData: string;

    @Prop({reflect: true, mutable: true}) business: boolean = false;

    componentWillLoad() {
        const registerDataElement = document.getElementById(this.registerData);
		const registerData = JSON.parse(registerDataElement.innerHTML) as RegisterData;

        Object.keys(registerData).map(key => {
			register.set(key as keyof RegisterData, registerData[key]);
		});

        loadCommonData(this.commonData, this.commonDynamicData, Build);
    }

    onBusiness(status: boolean = false) {
        this.business = status;
    }

	render() {
        const strings = register.get("strings");

		return <ks-page-base loadCommon={false} skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <div class="background">
                <h1>{strings.header}</h1>
                <p>{strings.message}</p>

                <ks-client-data-form password centered />
            </div>
        </ks-page-base>;
	}
}




